.tabs {
  width: 100%;
  text-align: center;
  margin: 0;
  padding: 0;
}

.tabs button {
  color: gray;
  background-color: #0000;
  border: none;
  padding: 15px 30px;
  font-family: Roboto Condensed;
  font-size: 20px;
  display: inline-block;
  position: relative;
}

.tabs button:after {
  content: "";
  height: 2px;
  width: 0;
  background: gray;
  transition: width .3s, left .3s;
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
}

.tabs button:hover, .tabs button:hover {
  cursor: pointer;
}

.tabs button:hover:after, .tabs button:focus:after {
  width: 80%;
  left: 10%;
}

.tabs .active {
  color: #000;
}

.tabs .active:after {
  content: "";
  height: 2px;
  width: 80%;
  background: #000;
  display: block;
  position: absolute;
  bottom: 10px;
  left: 10%;
}

